.container {
    max-width: 1200px;
    padding: 20px 60px 60px;
  }
  .nav {
    align-items: center;
    display: flex;
  }
  .nav-list {
    gap: 60px;
    margin-left: 222px;
    margin-right: 124px;
    display: flex;
  }
  .nav-btn {
    cursor: pointer;
    border: 1px solid azure;
    padding: 16px 31px;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.7;
  }
  .hero-section {
    padding-top: 209px;
    display: flex;
  }
  .hero-text-container {
    align-items: center;
    gap: 8px;
    font-size: 16px;
    display: flex;
  }
  .hero-head {
    width: 656px;
    margin-top: 16px;
    font-size: 72px;
    font-weight: 400;
  }
  .hero-overview {
    width: 615px;
    margin-top: 43px;
    font-size: 18px;
    font-weight: 400;
  }
  .hero-blok-btn {
    align-items: center;
    gap: 40px;
    margin-top: 53px;
    display: flex;
  }
  .hero-btn-explore {
    color: #fff;
    cursor: pointer;
    background-color: #57b8ff;
    padding: 21px 34px;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.7;
  }
  